import {DragDropModule} from '@angular/cdk/drag-drop';
import {ModuleWithProviders, NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {AdaptBadgeModule} from '../badge/index';
import {AdaptDropdownModule} from '../dropdown/index';
import {AdaptTabsControlsModule} from '../tabs-controls/index';

import {AdaptTabsPanelComponent} from './tabs-panel.component';
import {AdaptTabsComponent} from './tabs.component';

const ADAPT_TABS_DIRECTIVES = [
  AdaptTabsComponent,
  AdaptTabsPanelComponent
];

@NgModule({
  imports: [
    CommonModule,
    AdaptDropdownModule,
    AdaptTabsControlsModule,
    DragDropModule,
    AdaptBadgeModule
  ],
  exports: ADAPT_TABS_DIRECTIVES,
  declarations: [...ADAPT_TABS_DIRECTIVES]
})

export class AdaptTabsModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptTabsModule> {
    return {
      ngModule: AdaptTabsModule
    };
  }
}

export * from './tabs.component';
export * from './tabs-panel.component';
